/**
* Hotspot styles
*
* @file hotspot.css
* @author Vignesh Selvam
*/

.aquila-audio-player .progress-bar {
    background-color: #f9f9f9;
    box-shadow: none;
    height: auto;
    color: #74797b;
    font-size: 16px;
    float: none;
    height: inherit;
  }
  
  .regionContainer {
    top: 308px;
    position: absolute;
    left: 36%;
    width: 525px;
    z-index: 1201;
    background-color: #f9f9f9;
    display: none;
  }
  
  .regionCloseBtn {
    color: grey;
    float: right;
    font-size: 22px;
    font-weight: bold;
    padding: 0px 2px 2px 0px;
    display: none;
    background: none;
    border: none;
  }
  
  .regionCloseBtn:hover,
  .regionCloseBtn:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
  
  .hotspotContent {
    left: 40%;
    right: 50%;
    padding-left: 25px;
    padding-right: 25px;
    width: 150px;
    padding-top: 5px;
  }
  
  .videoContent {
    position: absolute;
    margin-top: 100px;
    margin-bottom: 100px;
    left: 25%;
    right: 25%;
    height: 150px;
    width: 150px;
  }

  .regionContainer .videoContent video {
    opacity: 1 !important;
  }

  .regionContainer .videoContent .video-poster {
    display: none;
  }

/* Smart pearson player Styles */

.sppModal {
  display: none;
  position: fixed;
  z-index: 1201;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #808080;
}

.sppCloseBtn {
  color: white;
  float: right;
  font-size: 34px;
  font-weight: bold;
  padding: 2px 16px;
}

.sppCloseBtn:hover,
.sppCloseBtn:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.sppModalHeader {
  padding: 2px 16px;
  background-color: #808080;
  color: white;
  font-weight: 600;
  font-size: 22px;
}

.sppTitle {
  color: white;
  font-weight: 600;
  font-size: 22px;
}